import React from 'react';
import request from 'superagent';

export default class Terminal extends React.Component {
  constructor(props) {
    super(props);

    this.processGcode = this.processGcode.bind(this);
  }

  processGcode(event) {
    event.preventDefault();
    console.log('sweet gcode', event.target.gcode.value);
  }

  render() {
    return (
      <div>
        <ul id="terminal-reply">
          <form id="gcode-terminal" onSubmit={this.processGcode}>
            <input id="gcode-input" type="text" name="gcode" defaultValue="" autocomplete="off" placeholder="" autofocus="autofocus" className="float-left"/>
            <input type="submit" value="Send GCode" className="float-right"/>
          </form>
        </ul>
      </div>
    );
  }
}
